<template>
  <div id="chartEarch" class="chart-earch"></div>
</template>

<script>
import echarts from 'echarts'
import 'echarts-gl'
import 'echarts/map/js/world.js' //
//import EN2CN from './EN2CN.json'
import SCATTER_3D_DATA from './SCATTER_3D_DATA.json'

const DATA = SCATTER_3D_DATA.map(item => [item[0], item[1], 1])

export default {
  async mounted() {
    this.initChart()
  },
  beforeDestroy() {
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
  },
  methods: {
    initChart() {
      var dom = document.getElementById('chartEarch')
      var myChart = echarts.init(dom)
      var canvas = document.createElement('canvas')
      var mapChart = echarts.init(canvas, null, {
        width: 4096,
        height: 2048
      })

      mapChart.setOption({
        //backgroundColor: '#000',
        visualMap: {
          show: false,
          min: 0,
          max: 2000,
          inRange: {
            //color: ['rgba(255,255,255,.1)'],
            //color: ['transparent']
            //colorAlpha: .1,
          }
        },
        series: [
          {
            type: 'map',
            map: 'world',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            boundingCoords: [[-180, 90], [180, -90]],
            label: {
              show: false
            },
            itemStyle: {
              borderColor: '#89a9d7',
              areaColor: 'transparent',
              //areaColor: "#1f3161",
              //backgroundColor: 'rgba(255,255,255,.1)',
              //color: "rgba(255,255,255,.1)",
              //opacity: .6
            },
            //
            emphasis: {
              label: {
                show: false,
                color: '#fff',
                fontSize: 22
              },
              itemStyle: {
                areaColor: 'transparent',
                //areaColor: 'rgba(255,255,255,.1)',
                borderColor: '#fff'
                //opacity: .1
              }
            },
            data: [
              //{name : 'China', value : 1359821.465},
            ],
            //
            //nameMap: EN2CN,
          }
        ]
      })

      // http://visibleearth.nasa.gov/view_cat.php?categoryID=1484
      const option = {
        title: {
          x: 'center',
          y: '70',
          textStyle: {
            color: '#f7f7f7'
          }
        },
        globe: {
          baseTexture: mapChart,
          //heightTexture: "/asset/get/s/data-1491889019097-rJQYikcpl.jpg",
          displacementScale: 0.9, //
          //shading: 'lambert',
          light: {
            ambient: {
              intensity: 1 //
            },
            main: {
              intensity: 0.5 //
            }
          },
          viewControl: {
            autoRotate: true, //
            autoRotateAfterStill: 1, //
            autoRotateDirection: 'ccw', // 'cw'  'ccw'
            autoRotateSpeed: 20, // /
            // 
            //rotateSensitivity: 0,
            // 
            rotateSensitivity: [1, 0],
            //0default: 1
            zoomSensitivity: 0,
            distance: 300 //
          }
        },
        series: {
          type: 'scatter3D',
          coordinateSystem: 'globe',
          //blendMode: 'lighter', //'source-over''lighter''source-over' alpha 'lighter'
          symbolSize: 1, //
          itemStyle: {
            color: 'rgba(137, 169, 215, .3)',
            //opacity: .5
          },
          //
          emphasis: {
            label: {
              show: false,
            },
            itemStyle: {
              areaColor: 'transparent',
              borderColor: '#fff'
            }
          },
          data: DATA
        }
      }
      // window.addEventListener('keydown', function () {
      //   myChart.dispatchAction({
      //     type: 'lines3DToggleEffect',
      //     seriesIndex: 0
      //   })
      // })
      if (option && typeof option === 'object') {
        myChart.setOption(option, true)
      }
      mapChart.on('click', this.handleChartsItem)
      //mapChart.getZr().on('click', this.handleChartsItem)

    },
    handleChartsItem(params) {
      console.log(params)
      if(params.target) {
        //alert(params.target.id)
      }
    }
  }
}
</script>
<style>
body{
  background: #1f274b;
}
.chart-earch {
  width: 600px;
  height: 600px;
  margin: 0 auto;
  background: #1f274b;
}
</style>

